.topbar {
   width: 100%;
   padding: 23px 46px;
   margin-left: -16%;
   margin-right: 40px;
   box-sizing: border-box;
   position: fixed;
   top: 0;
   box-shadow: 0 2px 4px #ccc;
   background-color: white;
   text-align: center;
}

.menuitem {
   margin-right: 16px;
   font-weight: 600;
   font-size: 14px;
   color: black;
   text-decoration: none;
}